d day of programming, life reminded us of itself, because it turned out that the Polish government was about to release exactly the same application. Seeing first reactions to the government version and knowing the quality of other government products, we decided to finish our prototype. We were all impressed by how much we were able to create in such a short time (as a group of 7 strangers)."}}),_c('div',{staticStyle:{"width":"100%","max-width":"500pt","margin":"16pt auto"}},[_c('vueper-slides',{staticClass:"no-shadow",staticStyle:{"width":"calc(100% - 112px)","margin":"0 auto"},attrs:{"fixed-height":"500px","infinite":false,"touchable":false,"arrows-outside":"","bullets":false}},_vm._l((_vm.slides),function(slide,i){return _c('vueper-slide',{key:i,staticStyle:{"background-size":"contain","background-repeat":"no-repeat"},attrs:{"image":slide.image}})}),1)],1),_c('ArticleText',{attrs:{"text":"Of course we all contributed a lot to this system, from management and design to backend and AI. But I will focus here on my problems and solutions."}}),_c('LinkWithDescription',{attrs:{"text":"If you want to read more about the backend, here is a link to a colleague's article:","url":"https://getindata.com/blog/fighting-COVID-19-Google-Cloud-quarantine-tracking-system"}}),_c('LeftHeader',{attrs:{"text":"My part"}}),_c('ArticleList',{attrs:{"header":"I personally spent about 70 hours during this hackathon to create a working prototype application. The final product had the following functionalities:","children":[
