ed to implement a satisfactory version for hackathon (without full FCM token variability support)."}}),_c('LeftHeader',{attrs:{"text":"Video"}}),_c('YtPlayer',{attrs:{"url":"https://www.youtube.com/embed/JhSBDsFtAM4"}})],1)}
